import { fetchImages } from './js/pixabay-api.js';
import { renderImages } from './js/render-functions.js';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const form = document.querySelector('#search-form');
const loader = document.querySelector('#loader');
const gallery = document.querySelector('.gallery');
const fetchPostsBtn = document.querySelector('.btn');
const inputEl = form.querySelector('input');

let page = 1;
const per_page = 40;
let searchQuery = '';

fetchPostsBtn.style.display = 'none';
loader.style.display = 'none';

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});

form.addEventListener('submit', async event => {
  event.preventDefault();
  searchQuery = inputEl.value.trim();

  if (!searchQuery) {
    iziToast.warning({ title: '–û—à–∏–±–∫–∞', message: '–í–≤–µ–¥–∏—Ç–µ –ø–æ–∏—Å–∫–æ–≤—ã–π –∑–∞–ø—Ä–æ—Å!' });
    return;
  }

  page = 1;
  gallery.innerHTML = '';
  fetchPostsBtn.style.display = 'none';
  loader.style.display = 'block';

  await loadImages(false);
});

fetchPostsBtn.addEventListener('click', async () => {
  page += 1;
  loader.style.display = 'block';
  await loadImages(true);
});

// ‚úÖ –§–∏–∫—Å —Å–∫—Ä—ã—Ç–∏—è –ª–æ–∞–¥–µ—Ä–∞ –∏ –ø–æ–∫–∞–∑–∞ –∫–Ω–æ–ø–∫–∏
async function loadImages(shouldScroll) {
  try {
    const data = await fetchImages(searchQuery, page, per_page);

    if (!data.hits || data.hits.length === 0) {
      iziToast.error({
        title: '–û—à–∏–±–∫–∞',
        message:
          'Sorry, there are no images matching your search query. Please try again!',
      });
      return;
    }

    renderImages(data.hits);
    lightbox.refresh();

    if (shouldScroll) {
      scrollPage();
    }

    if (page * per_page >= data.totalHits) {
      fetchPostsBtn.style.display = 'none';
      iziToast.info({
        title: '–ö–æ–Ω–µ—Ü —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤',
        message: "We're sorry, but you've reached the end of search results.",
      });
    } else {
      fetchPostsBtn.style.display = 'block';
    }
  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π:', error);
    iziToast.error({
      title: '–û—à–∏–±–∫–∞',
      message: '–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –±–æ–ª—å—à–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π',
    });
  } finally {
    loader.style.display = 'none';
  }
}
function scrollPage() {
  const galleryItems = document.querySelectorAll('.gallery-item');
  if (galleryItems.length > 0) {
    const { height } = galleryItems[0].getBoundingClientRect();
    window.scrollBy({
      top: height * 2,
      behavior: 'smooth',
    });
  }
}
